export default function ShowcasePage() {
  const [selectedTemplate, setSelectedTemplate] = useState<string | null>(null);
  const heroRef = useRef<HTMLDivElement>(null);
  const gridRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // Hero animation
    const hero = heroRef.current;
    if (hero) {
      const tl = gsap.timeline({ defaults: { ease: "power3.out" } });
      
      tl.fromTo(
        hero.querySelectorAll(".hero-badge"),
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.5 }
      )
      .fromTo(
        hero.querySelectorAll(".hero-title"),
        { opacity: 0, y: 30 },
        { opacity: 1, y: 0, duration: 0.7 },
        "-=0.3"
      )
      .fromTo(
        hero.querySelectorAll(".hero-desc"),
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.6 },
        "-=0.4"
      )
      .fromTo(
        hero.querySelectorAll(".hero-stat"),
        { opacity: 0, y: 15 },
        { opacity: 1, y: 0, duration: 0.4, stagger: 0.1 },
        "-=0.3"
      );
    }

    // Grid animation
    const grid = gridRef.current;
    if (grid) {
      const cards = grid.querySelectorAll(".template-card");
      gsap.fromTo(
        cards,
        { opacity: 0, y: 40, scale: 0.95 },
        {
          opacity: 1,
          y: 0,
          scale: 1,
          duration: 0.5,
          stagger: {
            amount: 0.4,
            from: "start",
          },
          ease: "back.out(1.2)",
          scrollTrigger: {
            trigger: grid,
            start: "top 85%",
          },
        }
      );
    }

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
    };
  }, []);

  return (
    <main className="min-h-screen px-3 sm:px-4 lg:px-6 xl:px-8 pb-12 sm:pb-20 pt-8 sm:pt-12">
      <section className="mx-auto flex w-full max-w-7xl flex-col gap-8 sm:gap-12">
        <SiteHeader />

        {/* Hero Section */}
        <div ref={heroRef} className="text-center max-w-3xl mx-auto">
          <div className="hero-badge">
            <Badge variant="glass" className="gap-1.5 text-xs sm:text-sm mb-3 sm:mb-4">
              <LayoutTemplate className="h-3 w-3 sm:h-4 sm:w-4" />
              Template Showcase
            </Badge>
          </div>
          
          <h1 className="hero-title text-2xl sm:text-3xl md:text-4xl lg:text-5xl font-semibold tracking-tight mb-3 sm:mb-4">
            <span className="bg-gradient-to-br from-foreground via-foreground to-muted-foreground bg-clip-text text-transparent">
              See your theme in action
            </span>
          </h1>
          
          <p className="hero-desc text-sm sm:text-base text-muted-foreground max-w-2xl mx-auto mb-6 sm:mb-8">
            Preview how your selected gradient theme looks across different application templates. 
            From landing pages to dashboards, see the full potential of your theme.
          </p>

          {/* Stats */}
          <div className="flex flex-wrap justify-center gap-3 sm:gap-4">
            {[
              { icon: LayoutTemplate, label: "6 Templates" },
              { icon: Palette, label: "21 Themes" },
              { icon: Zap, label: "Live Preview" },
            ].map(({ icon: Icon, label }) => (
              <div 
                key={label} 
                className="hero-stat flex items-center gap-1.5 sm:gap-2 text-xs sm:text-sm text-muted-foreground bg-background/60 px-3 sm:px-4 py-1.5 sm:py-2 rounded-full border border-border/50"
              >
                <Icon className="h-3.5 w-3.5 sm:h-4 sm:w-4 text-primary" />
                {label}
              </div>
            ))}
          </div>
        </div>

        {/* Templates Grid */}
        <div ref={gridRef} className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
          {templates.map((template) => {
            const Icon = template.icon;
            const PreviewComponent = previewComponents[template.preview];
            
            return (
              <div
                key={template.id}
                className="template-card group"
                onClick={() => setSelectedTemplate(template.id)}
              >
                <Card className="border-border/50 bg-background/60 overflow-hidden cursor-pointer transition-all duration-300 hover:shadow-xl hover:shadow-primary/10 hover:-translate-y-1">
                  {/* Preview Area */}
                  <div className={cn(
                    "relative h-36 sm:h-44 overflow-hidden bg-gradient-to-br",
                    template.color
                  )}>
                    <div className="absolute inset-0 p-3 sm:p-4">
                      <div className="w-full h-full rounded-lg overflow-hidden shadow-lg">
                        <PreviewComponent />
                      </div>
                    </div>
                    
                    {/* Popular Badge */}
                    {template.popular && (
                      <div className="absolute top-2 left-2 sm:top-3 sm:left-3">
                        <Badge variant="default" className="gap-1 text-[10px] sm:text-xs">
                          <Sparkles className="h-2.5 w-2.5 sm:h-3 sm:w-3" />
                          Popular
                        </Badge>
                      </div>
                    )}

                    {/* Hover Overlay */}
                    <div className="absolute inset-0 bg-black/0 group-hover:bg-black/20 transition-colors flex items-center justify-center opacity-0 group-hover:opacity-100">
                      <Button size="sm" variant="outline" className="gap-1.5 text-xs sm:text-sm bg-background/80 backdrop-blur-sm">
                        <Eye className="h-3.5 w-3.5 sm:h-4 sm:w-4" />
                        Preview
                      </Button>
                    </div>
                  </div>

                  <CardContent className="p-3 sm:p-4">
                    <div className="flex items-start justify-between gap-2 mb-2">
                      <div className="flex items-center gap-2">
                        <div className="h-7 w-7 sm:h-8 sm:w-8 rounded-lg bg-primary/10 flex items-center justify-center">
                          <Icon className="h-3.5 w-3.5 sm:h-4 sm:w-4 text-primary" />
                        </div>
                        <h3 className="font-semibold text-sm sm:text-base">{template.title}</h3>
                      </div>
                    </div>
                    
                    <p className="text-xs sm:text-sm text-muted-foreground mb-3 line-clamp-2">
                      {template.description}
                    </p>

                    <div className="flex flex-wrap gap-1.5">
                      {template.tags.map((tag) => (
                        <span 
                          key={tag}
                          className="text-[10px] sm:text-xs px-2 py-0.5 rounded-full bg-muted text-muted-foreground"
                        >
                          {tag}
                        </span>
                      ))}
                    </div>
                  </CardContent>
                </Card>
              </div>
            );
          })}
        </div>

        {/* CTA Section */}
        <div className="rounded-2xl sm:rounded-3xl border border-border/40 bg-background/50 p-6 sm:p-8 lg:p-12 text-center">
          <div className="max-w-2xl mx-auto">
            <div className="h-10 w-10 sm:h-12 sm:w-12 rounded-xl sm:rounded-2xl bg-primary/10 flex items-center justify-center mx-auto mb-3 sm:mb-4">
              <Palette className="h-5 w-5 sm:h-6 sm:w-6 text-primary" />
            </div>
            <h2 className="text-lg sm:text-xl lg:text-2xl font-semibold mb-2 sm:mb-3">
              Want to customize these templates?
            </h2>
            <p className="text-xs sm:text-sm text-muted-foreground mb-4 sm:mb-6">
              Head over to the Studio to tweak colors, adjust gradients, and see changes in real-time across all templates.
            </p>
            <div className="flex flex-wrap justify-center gap-2 sm:gap-3">
              <MagneticButton strength={0.15}>
                <Button variant="glow" size="sm" className="gap-1.5 text-xs sm:text-sm" asChild>
                  <a href="/studio">
                    Open Studio
                    <ArrowRight className="h-3.5 w-3.5 sm:h-4 sm:w-4" />
                  </a>
                </Button>
              </MagneticButton>
              <MagneticButton strength={0.15}>
                <Button variant="outline" size="sm" className="text-xs sm:text-sm" asChild>
                  <a href="/gallery">Browse Themes</a>
                </Button>
              </MagneticButton>
            </div>
          </div>
        </div>
      </section>
    </main>
  );
}
